
import StateJS from '@/js/state.js'

const use_root = { root: true }

////////////////////////////////////////////////////////////////////////////////

function Filter() {

}

Filter.prototype = {}


////////////////////////////////////////////////////////////////////////////////

const state = {
};

const filterObj = new Filter();
const state_rw = StateJS.createStateRW({ pk: "id", listname: "project_types" })
state_rw.init(state)
state_rw.setFilter(state, { text: "", filter: filterObj })
state_rw.setPage(state, { limit: 10, offset: 0 })



const getters = {
    list(state) {
        return state.items
    },

    all(state) {
        return state_rw.listAll(state)
    },
}

const mutations = {

    handle_vo(state, vo) {
        state_rw.reset(state)
        state_rw.updateVO(state, vo)
    },

    remove_item(state, item) {
        state_rw.remove(state, item.id)
    },
};

const actions = {
    fetch(context) {
        let method = 'GET'
        let type = 'project-types'
        return context.dispatch("rest/execute", { method, type }, use_root)
            .then((r) => {
                let vo = r.data;
                context.commit('handle_vo', vo)
            })
    },

    insert(context, { item }) {
        let method = 'POST'
        let type = 'project-types'
        let data = {
            project_types: [item]
        }
        return context.dispatch("rest/execute", { method, type, data }, use_root)
            .then(() => {
                // let vo = r.data;
                context.dispatch('fetch')
            })
    },

    update(context, { item }) {
        let method = 'PUT'
        let type = 'project-types'
        let id = item.id;
        let data = {
            project_types: [item]
        }
        return context.dispatch("rest/execute", { method, type, id, data }, use_root)
            .then(() => {
                // let vo = r.data;
                context.dispatch('fetch')
            })
    },

    remove(context, item) {
        let method = 'DELETE'
        let type = 'project-types'
        let id = item.id
        return context.dispatch("rest/execute", { method, type, id }, use_root)
            .then(() => {
                // let vo = r.data;
                context.commit('remove_item', item)
            })
    },

};


export default {
    name: "module_project_type",
    namespaced: true,
    state, mutations, actions, getters
}
