<template>
  <div>
    <MyDialog v-model="displayNewVersion" />
  </div>
</template>

<script>
import MyDialog from "./new-version-dialog.vue";

export default {
  name: "check-update-auto",

  components: { MyDialog },

  data() {
    return {
      displayNewVersion: false,
    };
  },

  methods: {
    check() {
      let vo = {
        checkupdate: { auto: true },
      };
      this.$store.dispatch("checkupdate/check", vo).then((r) => {
        this.showResult(r.data);
      });
    },

    showResult(vo) {
      let cu = vo.checkupdate;
      let has_ignored = cu.has_ignored;
      let has_new = cu.has_new_version;
      if (has_new && !has_ignored) {
        this.displayNewVersion = true;
      }
    },
  },

  mounted() {
    this.check();
  },
};
</script>
